import React, { useState, useRef } from 'react'
import css from './Home.module.scss'
import Container from 'components/Container/Container'
import Form from 'components/Form/Form'
import Template from 'components/Template/Template'
import ReactToPrint from 'react-to-print'
import { COUNTRY_OPTIONS } from 'utils/const'

const Home = () => {
  const [formState, updateFormState] = useState({
    sex: true,
    code: '1234567890128',
    title: 'Yeezy boost 350 v2',
    sizeUSA: '8',
    sizeUK: '7 1/2',
    sizeFrance: '41 1/3',
    sizeDeutsch: '7 1/2',
    sizeJapan: '260',
    sizeChina: '255',
    model: 'FV3254',
    color: 'LUNDRF',
    country: COUNTRY_OPTIONS.find(item => item.value.toUpperCase() === 'CHINA'),
    barcodeLine1: 'EAN PO# 123322707',
    barcodeLine2: 'ABSBOX/0314/V10'
  })

  const updateFormData = ({field, value}) => {
    if (field === 'code') {
      if (Number.isNaN(+value[value.length - 1])) {
        updateFormState(prevState => ({
          ...prevState,
          [field]: formState.code,
        }))
      }

      if (value.length > 13 && formState.code.length === 13) {
        updateFormState(prevState => ({
          ...prevState,
          [field]: formState.code,
        }))
      }


      if (value.length === 0) {
        updateFormState(prevState => ({
          ...prevState,
          [field]: '',
        }))
      }

      if (value.length <= 13 && !Number.isNaN(+value[value.length - 1])) {
        updateFormState(prevState => ({
          ...prevState,
          [field]: value,
        }))
      }
    } else {
      updateFormState(prevState => ({
        ...prevState,
        [field]: value,
      }))
    }
  }

  const templateRef = useRef(null)

  return (
    <Container className={css.wrapper}>
      <h1 className={css.heading}>
        Конструктор наклеек Adidas
      </h1>
      <Form
        state={formState}
        stateUpdater={updateFormData}
      />
      <ReactToPrint
        trigger={() => (
          <button
            className={css.button}
            type={ `button` }
          >
            Отправить на печать
          </button>
        )}
        content={() => templateRef.current}
      />
      <Template
        className={css.template}
        state={formState}
      />
      <div className={css.printable} ref={templateRef}>
        { new Array(8)
          .fill('')
          .map((_, index) => (
            <Template
              className={css.templatePrint}
              state={formState}
              key={index}
            />
          ))
        }
      </div>
    </Container>
  )
}

export default Home
