import React from 'react'
import css from './Template.module.scss'
import classnames from 'classnames'
import IconMale from 'assets/icons/IconMale'
import IconFemale from 'assets/icons/IconFemale'
import Barcode from 'components/Barcode/Barcode'
import { renderFractions } from 'utils'
import validbarcode from 'barcode-validator'

const Template = ({className, state}) => {
  const {
    sex,
    title,
    code,
    sizeUSA,
    sizeUK,
    sizeFrance,
    sizeDeutsch,
    sizeJapan,
    sizeChina,
    model,
    color,
    country,
    barcodeLine1,
    barcodeLine2,
  } = state

  return (
    <div className={classnames(css.wrapper, className)}>
      <div className={css.header}>
        {sex
          ? <IconMale className={css.icon} />
          : <IconFemale className={css.icon} />
        }
        <h2 className={css.name}>
          { title }
        </h2>
      </div>
      <div className={css.body}>
        <dl className={css.sizes}>
          <div className={css.item}>
            <dt className={css.key}>
              US
            </dt>
            <dd className={css.value} dangerouslySetInnerHTML={{ __html: renderFractions(sizeUSA) }} />
          </div>
          <div className={css.item}>
            <dt className={css.key}>
              UK
            </dt>
            <dd className={css.value} dangerouslySetInnerHTML={{ __html: renderFractions(sizeUK) }} />
          </div>
          <div className={css.item}>
            <dt className={css.key}>
              F
            </dt>
            <dd className={css.value} dangerouslySetInnerHTML={{ __html: renderFractions(sizeFrance) }} />
          </div>
          <div className={css.item}>
            <dt className={css.key}>
              D
            </dt>
            <dd className={css.value} dangerouslySetInnerHTML={{ __html: renderFractions(sizeDeutsch) }} />
          </div>
          <div className={css.item}>
            <dt className={css.key}>
              J
            </dt>
            <dd className={css.value} dangerouslySetInnerHTML={{ __html: renderFractions(sizeJapan) }} />
          </div>
          <div className={css.item}>
            <dt className={css.key}>
              CHN
            </dt>
            <dd className={css.value} dangerouslySetInnerHTML={{ __html: renderFractions(sizeChina) }} />
          </div>
        </dl>
        <div className={css.info}>
          <p className={css.model}>
            { model }
          </p>
          <p className={css.label}>
            <span>
              { `${color}/${color}/${color}` }<br/>
            </span>
            <span>
              { `${color}/${color}/${color}` }
            </span>
          </p>
          <span className={classnames(css.label, css.originals)}>
            <span>
              ORIGINALS<br/>
            </span>
            <span>
              ORIGINALS
            </span>
          </span>
          <span className={css.manufacturer} dangerouslySetInnerHTML={{ __html: `MADE IN ${country.value.toUpperCase()}<br/>fabriqué en ${country.french.toUpperCase()}` }} />
        </div>
        <div>
          <div>
            {code.length === 13 && validbarcode(code) && <Barcode value={code} />}
            {code.length === 13 && !validbarcode(code) && <span className={css.error}>Invalid EAN13 barcode!</span>}
          </div>
          <p className={css.annex} dangerouslySetInnerHTML={{ __html: `${barcodeLine1}<br />${barcodeLine2}` }} />
        </div>
      </div>
    </div>
  )
}

export default Template
