import React from 'react'
import css from './Form.module.scss'
import Input from 'components/Input/Input'
import Checkbox from 'components/Checkbox/Checkbox'
import SelectDropdown from 'components/SelectDropdown/SelectDropdown'
import { COUNTRY_OPTIONS } from 'utils/const'

const Form = ({state, stateUpdater}) => {
  const {
    sex,
    title, code,
    sizeUSA,
    sizeUK,
    sizeFrance,
    sizeDeutsch,
    sizeJapan,
    sizeChina,
    model,
    color,
    country,
    barcodeLine1,
    barcodeLine2
  } = state

  return (
    <form>
      <div className={css.wrapper}>
        <Input
          className={css.name}
          label={ `Название` }
          value={ title }
          onChange={evt => stateUpdater({field: 'title', value: evt.target.value})}
          name={ `title` }
          type={ `textarea` }
        />
        <Input
          className={css.code}
          label={ `Штрихкод` }
          value={ code }
          onChange={evt => stateUpdater({field: 'code', value: evt.target.value})}
          name={ `code` }
        />
        <div className={css.dual}>
          <Input
            label={ `Номер модели` }
            value={ model }
            onChange={evt => stateUpdater({field: 'model', value: evt.target.value})}
            name={ `model` }
          />
          <Input
            label={ `Цвет модели` }
            value={ color }
            onChange={evt => stateUpdater({field: 'color', value: evt.target.value})}
            name={ `color` }
          />
          <Input
            label={ `Подпись под штрихкодом - строка 1` }
            value={ barcodeLine1 }
            onChange={evt => stateUpdater({field: 'barcodeLine1', value: evt.target.value})}
            name={ `barcodeLine1` }
          />
          <Input
            label={ `Подпись под штрихкодом - строка 2` }
            value={ barcodeLine2 }
            onChange={evt => stateUpdater({field: 'barcodeLine2', value: evt.target.value})}
            name={ `barcodeLine2` }
          />
        </div>
        <div className={css.sizes}>
          <Input
            className={css.size}
            label={ `Размер USA` }
            value={ sizeUSA }
            onChange={evt => stateUpdater({field: 'sizeUSA', value: evt.target.value})}
            name={ `sizeUSA` }
          />
          <Input
            className={css.size}
            label={ `Размер UK` }
            value={ sizeUK }
            onChange={evt => stateUpdater({field: 'sizeUK', value: evt.target.value})}
            name={ `sizeUK` }
          />
          <Input
            className={css.size}
            label={ `Размер France` }
            value={ sizeFrance }
            onChange={evt => stateUpdater({field: 'sizeFrance', value: evt.target.value})}
            name={ `sizeFrance` }
          />
          <Input
            className={css.size}
            label={ `Размер Deutsch` }
            value={ sizeDeutsch }
            onChange={evt => stateUpdater({field: 'sizeDeutsch', value: evt.target.value})}
            name={ `sizeDeutsch` }
          />
          <Input
            className={css.size}
            label={ `Размер Japan` }
            value={ sizeJapan }
            onChange={evt => stateUpdater({field: 'sizeJapan', value: evt.target.value})}
            name={ `sizeJapan` }
          />
          <Input
            className={css.size}
            label={ `Размер China` }
            value={ sizeChina }
            onChange={evt => stateUpdater({field: 'sizeChina', value: evt.target.value})}
            name={ `sizeChina` }
          />
        </div>
        <div className={css.sex}>
          <span>Жен</span>
          <Checkbox
            checked={sex}
            onChange={() => stateUpdater({field: 'sex', value: !sex})}
          />
          <span>Муж</span>
        </div>
        <div>
          <SelectDropdown
            label={ `Страна производства` }
            onChange={evt => stateUpdater({field: 'country', value: evt})}
            value={country}
            options={COUNTRY_OPTIONS}
          />
        </div>
      </div>
    </form>
  )
}

export default Form
