import React from 'react'
import css from './Barcode.module.scss'
import { useBarcode } from 'react-barcodes'

const Barcode = ({value}) => {
  const test = useBarcode({
    value: value,
    options: {
      format: 'ean13',
      font: 'Arial',
      fontSize: 20,
      background: 'transparent',
      height: '200rem',
    }
  })

  return (
    <svg className={css.barcode} ref={test.inputRef} />
  )
}

export default Barcode
